  
  .data-card {
    display: flex;
    flex-direction: column;
    max-width: 20.75em;
    min-height: 20.75em;
    overflow: hidden;
    border-radius: 0.5em;
    text-decoration: none;
    background: white;
    margin: 1em;
    padding: 1.5em 1.5em;
    box-shadow: 0 1.5em 2.5em -0.5em rgba(0, 0, 0, 0.1);
    transition: transform 0.45s ease, background 0.45s ease;
  }
  .data-card h3 {
    color: #2E3C40;
    font-size: 3.5em;
    font-weight: 600;
    line-height: 1;
    padding-bottom: 0.5em;
    margin: 0 0 0.142857143em;
    border-bottom: 2px solid #753BBD;
    transition: color 0.45s ease, border 0.45s ease;
  }
  .data-card h4 {
    color: black;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 800;
    line-height: 1.4;
    letter-spacing: 0.1em;
    margin: 25px 0 10px;
    transition: color 0.45s ease;
  }
  .data-card .cardin {
    opacity: 0;
    color: white !important;
    font-weight: 500;
    line-height: 1.2;
    margin: 1rem 3px 0em;
    transform: translateY(-1em);
    transition: opacity 0.45s ease, transform 0.5s ease;
    z-index: 99;
  }
  .data-card .link-text {
    display: block;
    color: #A78BFA;
    font-size: 1em;
    font-weight: 600;
    line-height: 1.2;
    margin: auto auto -1rem;
    transition: color 0.45s ease;
  }
  .data-card .link-text svg {
    margin-left: 0.5em;
    transition: transform 0.6s ease;
  }
  .data-card .link-text svg path {
    transition: fill 0.45s ease;
  }
  .data-card:hover {
    background: #753BBD;
    transform: scale(1.02);
  }

  .data-card:hover img {
    opacity: 0.75;
  }
  .data-card:hover h3 {
    color: #FFFFFF;
    border-bottom-color: #A754C4;
  }
  .data-card:hover h4 {
    color: #FFFFFF;
    opacity:0;
  }
  .data-card:hover .cardin {
    opacity: 1;
    transform: none;
    font-size:1rem;
  }
  .data-card .desmotA,.data-card .desmotA span  {
    color: #753BBD;
  }

  .data-card:hover .desmotA {
    opacity: 0;
  }
  .data-card:hover .link-text {
    color: #FFFFFF;
  }
  .data-card:hover .link-text svg {
    animation: point 1.25s infinite alternate;
  }
  .data-card:hover .link-text svg path {
    fill: #FFFFFF;
  }


  @keyframes point {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(0.125em);
    }
  }

  .btnPlayTrailer {
    opacity: 0;
    position: absolute;
    z-index: 999;
    font-size: 50px;
    color:white;
    top:30%;
    left:40%;
    transition: all 0.45s ease;

  }

  .data-card:hover .btnPlayTrailer {
    opacity: 1;
  }