.fullPage {
    background:url(../../../assets/images/bgImage.jpg);
    /* position: relative; */
    z-index: 0;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}


.hien {
    opacity: 1 !important ;
    visibility: visible !important;
    transition: 0.4s;
}

.bookTheaterdichuyen {
    left: -88px !important;;
}

.overlaydichuyen {
    left:0 !important;
}

.bookTheater-div_full {
    position: absolute;
    height: 100%;
    width:100%;
    padding:20px 0 50px 100px;
    z-index: 1;
}

.bookTheater-div_full .container {
    position: relative!important ;
}

.bookTheater {
    width: 25%;
    height: 77px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 49;
    opacity: 0;
    /* visibility: hidden; */
    transition: all 0.5s;
}
.bookTheater--navbar {
    text-align: center;
    background-color: var(--color-main1);
    /* background: url("../../assets/images/backgroundBook.jpg") no-repeat center; */
    background-size: cover;
    width: 70px;
    height: 100%;
    position: relative;
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    color: #ffffff;
    font-weight: 700;
    transition: 0.5s;
}
.bookTheater--navbar__text{
    transition: 0.5s;
    margin-right: -60px;
    transform: translateY(-100px);
}
.bookTheater--navbar:hover{
    opacity: 1;
    width: 190px;
}
.bookTheater--navbar:hover .bookTheater--navbar__text{
    transform: translateY(0);

}
.bookTheater--navbar:before {
    position: absolute;
    content: "";
    top: -45px;
    right: -50px;
    border: none;
    border-top: 59px solid transparent;
    border-bottom: 59px solid transparent;
    border-right: 59px solid var(--color-main1);
    transform: rotateZ(45deg);
}
.bookTheater--navbar__tap {
    position: absolute;
    right: 0;
    height: 40px;
    top: 17px;
}
.bookTheater--navbar:hover .bookTheater--navbar__tap{
    right:120px;
    animation: tap 2s forwards;
}
.bookTheater--navbar__tap img {
    height: 100%;
 
}
@keyframes tap {
    0% {
    -webkit-transform: rotate(0) scale(1) skew(1deg);
    }
    10% {
    -webkit-transform: rotate(-25deg) scale(1) skew(1deg);
    }
    20% {
    -webkit-transform: rotate(25deg) scale(1) skew(1deg);
    }
    30% {
    -webkit-transform: rotate(-25deg) scale(1) skew(1deg);
    }
    40% {
    -webkit-transform: rotate(25deg) scale(1) skew(1deg);
    }
    50% {
    -webkit-transform: rotate(0) scale(1) skew(1deg);
    }
    100% {
    -webkit-transform: rotate(0) scale(1) skew(1deg);
    }
} 
.bookTheater-div {
    position: fixed;
    top: 0;
    transition: 0.5s;
    left: -100%;
    width: 90%;
    height: 100%;
    z-index: 10000;

}
.bookTheater-div::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    left: 0%;
    background-image: url(../../../assets/images/backgroundBook.jpg);
    -webkit-transform: skewX(214deg);
    -moz-transform: skewX(8deg);
    -ms-transform: skewX(8deg);
    -o-transform: skewX(8deg);
    transform: skewX( 167deg);
}
.bookTheater-div:after{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    left: 0;
    background: url(../../../assets/images/pattern.jpg);
    -webkit-transform: skewX(214deg);
    -moz-transform: skewX(8deg);
    -ms-transform: skewX(8deg);
    -o-transform: skewX(8deg);
    transform: skewX( 167deg);
    opacity: 0.13;
}
.bookTheater-div_close{
    position: absolute;
    top:10px;
    right: -47px;
    font-size: 30px;
    color:#ffffff;
    cursor: pointer;
    z-index: 10001;

}
.bookTheater-overlay{
    position: fixed;
    transition: 0.7s;
    top: 0;
    left: -120%;
    width: 110%;
    height: 100%;
    z-index: 9999;
    background: rgb(0 0 0 / 70%);
    -webkit-transform: skewX(214deg);
    -moz-transform: skewX(8deg);
    -ms-transform: skewX(8deg);
    -o-transform: skewX(8deg);
    transform: skewX( 167deg);
}